var grabScroller = function() {
	var scrollStart=0;
	var scrollOffsetStart = 0;
	var scrollObj;
	var childWidth;
	var scrolled = false;
	var activePosition;

	function datepickerScrollHandler(e) {
		var distance=scrollStart-e.pageX;
		$(scrollObj).scrollLeft(distance+scrollOffsetStart);
		scrolled=true;
	}
	function snapToNearest(e) {
		console.log("snap to nearest",e);
		var position=Math.round($(scrollObj).scrollLeft()/childWidth);
		var target = position * childWidth;
		// $(scrollObj).scrollLeft(target);
		$(scrollObj).animate({
			scrollLeft: target
		}, 400);
		activePosition = position;
		return position;
	}
	return {
		Init: function(jqueryObj) {
			
			scrollObj = jqueryObj[0];
			childWidth = $(scrollObj).find("li:first-child").width();

			jqueryObj.unbind("mousedown");
			$("body").unbind("movemove").unbind("mouseup");
			jqueryObj.bind("mousedown",function(e) {
				scrolled = false;
				scrollStart=e.pageX;
				scrollOffsetStart=$(this).scrollLeft();
				$("body").bind("mousemove",datepickerScrollHandler);
				$(jqueryObj).find("li").bind("mouseup",function() {
					if (!scrolled) {
						grabScroller.scrollTo($(this).index()*childWidth);
					}
				});
				$("body").bind("mouseup",function(e) {
					$("body").unbind("mousemove",datepickerScrollHandler);
					$("body").unbind("mouseup");
					
					if (scrolled) {
						var target = snapToNearest();
						scrollEndHandler(target);
					} 
				});
			});
			var length = jqueryObj.children().length;
			
			var center = jqueryObj.find(".today").index();
			if (center==-1) {
				center = (length-1)/2;
			}
			console.log(jqueryObj,length, center);
			grabScroller.scrollTo(center*childWidth);
		},
		scrollTo: function(point) {
			var position=Math.round(point/childWidth);
			var target = position * childWidth;
			$(scrollObj).animate({
				scrollLeft: target
			}, 300,function() {
				scrollEndHandler(position);
			});
		},
		getPosition: function() {
			if (typeof scrollObj != "undefined") {
				return activePosition;
			} else {
				return 5;
			}
		}
	}
	
}();

