function buildItinerary(full) {



    var target=document.location.hash.replace("#","");
	
	var journey = entireJourney.outJourney;
	if (typeof entireJourney.outJourney =="undefined") {
		document.location.hash='/get_tickets/outbound/';
		return;
	}

	var nav = '<ol><li class="outbound active">Outbound itinerary</li><li>Return itinerary</li></ol>';
	if (typeof entireJourney.outJourney =="undefined" && full ) document.location.hash='/get_tickets/outbound/';

	if (full || target.indexOf('outbound')>-1) {
		journeyStops='<ul class="active"><li><p>Please make sure to arrive at the station well before your scheduled departure.</p><p><a href="#" onclick="return false" class="mapLink">Show directions to the coach station</a></p></li>';
		var time = pad(journey.time,4);
		var tripTime=new Date(now.getMonth()+"/"+now.getDate()+"/"+now.getFullYear()+" "+time.substring(0,2)+":"+time.substring(2,4)+":00");

	    for (i=0;i<journey.leg.length;i++) {
	    	for (a=0;a<journey.leg[i].stop.length;a++) {
	    		
	    		var classname="stop"
	    		var extra="";
	    		var tempTime = new Date(tripTime.getTime() + journey.leg[i].stop[a].departure_time * 60000 );
	    		if (a==0) {
	    			classname="stop newleg";
	    			extra='<div class="carrier">'+journey.leg[i].carrier+'</div>';
	    		} else if (a == journey.leg[i].stop.length-1) {
	    			var tempTime = new Date(tripTime.getTime() + journey.leg[i].stop[a].arrival_time * 60000 );
	    			extra = '';
	    			classname="stop endleg";
	    		}
	    		var time = "<time>"+pad(tempTime.getHours(),2)+':'+pad(tempTime.getMinutes(),2)+"</time>";
	    		journeyStops+='<li class="'+classname +'">'+time+journey.leg[i].stop[a].city+extra+'</li>';
	    		if (a == journey.leg[i].stop.length-1 && i<journey.leg.length-1) {
	    			journeyStops+='<li class="layover">Layover in ' + journey.leg[i].stop[a].city +extra +'</li>';
	    		}
	    	}
	    }

	    journeyStops+="</ul>";
	    outJourneyHTML=journeyStops;
	}

    
    journey = entireJourney.returnJourney;

	
	if (full || target.indexOf('return')>-1) {
		journeyStops='<ul><li><p>Please make sure to arrive at the station well before your scheduled departure.</p><p><a href="#" onclick="return false" class="mapLink">Show directions to the coach station</a></p></li>';
		var time = pad(journey.time,4);
		var tripTime=new Date(now.getMonth()+"/"+now.getDate()+"/"+now.getFullYear()+" "+time.substring(0,2)+":"+time.substring(2,4)+":00");

	    for (i=0;i<journey.leg.length;i++) {
	    	for (a=0;a<journey.leg[i].stop.length;a++) {
	    		
	    		var classname="stop"
	    		var extra="";
	    		var tempTime = new Date(tripTime.getTime() + journey.leg[i].stop[a].departure_time * 60000 );
	    		if (a==0) {
	    			classname="stop newleg";
	    			extra='<div class="carrier">'+journey.leg[i].carrier+'</div>';
	    		} else if (a == journey.leg[i].stop.length-1) {
	    			var tempTime = new Date(tripTime.getTime() + journey.leg[i].stop[a].arrival_time * 60000 );
	    			extra = '';
	    			classname="stop endleg";
	    		}
	    		var time = "<time>"+pad(tempTime.getHours(),2)+':'+pad(tempTime.getMinutes(),2)+"</time>";
	    		journeyStops+='<li class="'+classname +'">'+time+journey.leg[i].stop[a].city+extra+'</li>';
	    		if (a == journey.leg[i].stop.length-1 && i<journey.leg.length-1) {
	    			journeyStops+='<li class="layover">Layover in ' + journey.leg[i].stop[a].city +extra +'</li>';
	    		}
	    	}
	    }

    	journeyStops+="</ul>";
   		returnJourneyHTML=journeyStops;
   	}
    
    if (full) {
    	$(".itinerary .container").html(nav+outJourneyHTML+returnJourneyHTML+'<img src="img/itinerary-notes.png" width="100%">');
    } else {
    	console.log("check",outJourneyHTML)
    	if (target.indexOf('outbound')>-1) {
    		$(".itineraryDrawer .container").html(outJourneyHTML+'<img src="img/itinerary-notes.png" width="100%">');
    	} else {
    		$(".itineraryDrawer .container").html(returnJourneyHTML+'<img src="img/itinerary-notes.png" width="100%">');
    	}
    }
    $(".itinerary .container ol li").bind("click",itineraryClickHandler);
}
function itineraryClickHandler(e) {
	thisNo = $(e.target).index();
	$(e.target).addClass("active").siblings().removeClass("active");
	$(".itinerary .container ul").removeClass("active");
	$(".itinerary .container ul:nth-of-type("+(thisNo+1)+")").addClass("active");
}

function openItineraryDrawer(obj) {
	var target=document.location.hash.replace("#","");
	if (target.indexOf('outbound')>-1) {
		entireJourney.outJourney = $(obj).closest("li")[0].journeyData;
	} else {
		entireJourney.returnJourney = $(obj).closest("li")[0].journeyData;
	}

	buildItinerary(false);
	$(".itineraryDrawer").addClass("active");
}
$(function() {
	$(".itineraryDrawer .close_button").bind("click",function() {
		$(".itineraryDrawer").toggleClass("active");
	})
})