function people(e) {
	$(".popover.people").toggleClass("show");
	if ($(e).hasClass("btn")) {
		if ($("#spinner").val() == 1) {
			$(".btn.people p").html($("#spinner").val() + " person");
		} else {
			$(".btn.people p").html($("#spinner").val() + " persons");
		}
	}
}
$(function() {

	$("#spinner").spinner({
		min: 1
		, max: 9
	});
	$("#spinner2").spinner({
		min: 0
		, max: 9,
		val:0
	});
	$("#spinnerCard").spinner({
		min: 0
		, max: numberTraveller
	});
	$('.ui-spinner-button').click(function() {
		$(this).siblings('input').change();
	});

	$("#spinner").bind("change",function() {
		var thisValue=$(this).val();
		$("#spinner2").spinner("option","max",thisValue);
		if ($("#spinner2").val()>thisValue) {
			$("#spinner2").val(thisValue);
		}
	});

	$(".people").on('click', '.ui-spinner-button', function (e) {
		e.preventDefault();
		numberTraveller = $(this).parent().find("input").val();
		if (numberTraveller == 1) {
			$(".cards .travellers").html("For 1 traveller");
			$(".ticket_finder header .initial .summary").html("Return trip, 1 traveller");
		} else {
			$(".cards .travellers").html("For "+numberTraveller+" travellers");
			$(".ticket_finder header .initial .summary").html("Return trip, "+numberTraveller+" travellers");
		}
		if ($("#spinner").val() == 1) {
			$(".btn.peopleButton p").html($("#spinner").val() + " person");
		} else {
			$(".btn.peopleButton p").html($("#spinner").val() + " persons");
		}
		var minValue = parseInt($(this).parent().find("input").spinner("option","min"));
		var maxValue = parseInt($(this).parent().find("input").spinner("option","max"));
		if (numberTraveller < minValue+1) {
			$(this).parent().find(".ui-spinner-down").css("opacity", "0.4")
		} else if (numberTraveller >= maxValue) {
			$(this).parent().find(".ui-spinner-up").css("opacity", "0.4")
		} else {
			$(this).parent().find(".ui-spinner-down, .ui-spinner-up").css("opacity", "1")
		}
		return false;
	});
});