var currentPage;
var hashChangeHandler = function() {
	var prevHash='';
	function setupLinks() {
		
	}
	function change() {
		ticketLockedIn = false;
		var target=document.location.hash.replace("#","");
		console.log("location hash",target);
		currentPage=target;

		$("div.cards").css("opacity","");

		if (target.indexOf("/station_info/")>-1) {
			target = target.replace("/station_info/","");
			openStationInfo(target);
		}
		if (target.indexOf('/ticket_details/')>-1) {

			tripLockedIn=true;
			$(".ticket_details").addClass("enabled");
			window.requestAnimationFrame(function() {
				drawFinalJourney(searchHandler.getJourney());
			});
			closeDrawers();
			$(".ticket_finder").removeClass("outbound return summary")
		} else {
			$(".ticket_details").removeClass("enabled");
			undrawJourney();
			if (prevHash.indexOf('/ticket_details/')>-1) updateMarkers();
		}
		if (target.indexOf('/get_tickets/')>-1) {
			$("body").addClass("tickets");
			$("div.cards").css("opacity",null);
			if (prevHash.indexOf('/ticket_details/')>-1) setTimeout(showLoadingScreen,900);
			var delay=2000;
			if (prevHash.indexOf('/get_tickets/')>-1) delay=0;
			setTimeout(function(target,prevHash) {
				initiateTicketing(target, (prevHash.indexOf('/get_tickets/')==-1));
			},delay,target,prevHash);
		} else {
			$("body").removeClass("tickets");
		}
		prevHash = target;
	}
	return {
		Init: function() {
			$(window).bind("hashchange",change);
			change();
		}
	}
	
}();

