var sortOptions='';
var dropDownHandler = function() {

	function clickHandler(e) {
		var thisDropdown = $(e.target).closest(".formDropdown");
		thisDropdown.toggleClass("active");
		$(document).bind("mouseup", function (e) {
			if (e.target.tagName=="H4" && e.target.parentNode.className=="sortoptions") {
				thisDropdown.removeClass("active");
			} else if (!thisDropdown.is(e.target) && thisDropdown.has(e.target).length === 0) {
		       	thisDropdown.removeClass("active");
		    }
		});
	}
	function radioHandler(e) {
		var thisDropdown = $(e.target).closest(".formDropdown");
		sortOptions = thisDropdown.find("input:checked").val();
		value=thisDropdown.find("input:checked + label p").html().toLowerCase();
		thisDropdown.find(".sortlabel").html(value);
		doFilter();
		setTimeout(function(thisDropdown) {
			thisDropdown.removeClass("active");
		},700,thisDropdown);
	}
	return {
		Init: function(elm) {
			$(".formDropdown").each(function() {
				$(this).find("input[type=radio]").bind("click",radioHandler);
				$(this).find(".sortingby").bind("click",clickHandler);
			});
		}
	}
}();
$(function() {
	dropDownHandler.Init();
});